import React, { useEffect, useRef, useState } from 'react'
import styles from './index.module.less'
import Header from '../../components/Header'
import { Button, CapsuleTabs, Toast, Mask, SpinLoading, Modal, Input } from 'antd-mobile'
import axios from 'axios'
export default function Wallpaper() {
  const imgRef = useRef()
  const [load, setLoad] = useState(false)
  const [url, setUrl] = useState('')
  const [type, setType] = useState('mobile')
  const [vip, setVip] = useState(false)
  const [modal, setModal] = useState(false)
  const [pwd, setPwd] = useState('')
  const [pcdisable, setPcdisable] = useState(false)
  const imgLoad = () => {
    //
    let timer = setInterval(() => {
      if (imgRef.current.complete) {
        clearInterval(timer)
        //
        setLoad(false)
      }
    }, 50)
  }
  const loadimg = (type) => {
    //
    if (!vip) {
      axios.get(`https://api.btstu.cn/sjbz/api.php?format=json&method=${type}`).then(res => {
        setUrl(res.data.imgurl)
        imgLoad()
      })
    } else {
      axios.get('/api3/api?r18=true').then(res => {
        setUrl(res.data.data.url[0])
        setLoad(true)
        imgLoad()
      })
    }
  }
  const changeType = (e) => {
    //pc/pe
    setType(e)
    loadimg(e)
    setLoad(true)
  }
  const changeimg = () => {
    //
    loadimg(type)
    setLoad(true)
  }
  const submit = () => {
    axios.get('https://1-1302996373.cos.ap-nanjing.myqcloud.com/password.txt').then(res => {
      setModal(false)
      setPwd('')
      if (res.data !== pwd) {
        Toast.show({ content: '' })
      } else {
        Toast.show({ content: '' })
        setVip(true)
        setPcdisable(true)
      }
    })
  }
  useEffect(() => {
    Modal.alert({
      title: '',
      content:
        <div>
          <p>1.</p>
          <p>2.</p>
          <p>3.</p>
        </div>
    })
  }, [])
  useEffect(() => {
    //vip
    setLoad(true)
    loadimg('mobile')
  }, [vip])
  return (
    <>
      <Header title='' />
      <div className={styles.container}>
        <img ref={imgRef} onClick={changeimg} src={url} alt="" />
        <CapsuleTabs onChange={changeType} defaultActiveKey='mobile'>
          <CapsuleTabs.Tab disabled={pcdisable} title='' key='pc' />
          <CapsuleTabs.Tab title='' key='mobile' />
        </CapsuleTabs>
        <Button block color='primary' onClick={() => {
          Toast.show({
            content: ''
          })
        }}></Button>
        <p className={styles.tips} onClick={() => { setModal(true) }}></p>
      </div>
      <Modal visible={modal} content={(
        <>
          <Input value={pwd} onChange={(e) => setPwd(e)} placeholder='' />
          <Button block color='primary' onClick={submit}></Button>
        </>
      )} />
      <Mask visible={load}>
        <SpinLoading style={{ '--size': '40px', margin: '40vh auto 0 auto' }} color='white' />
      </Mask>
    </>
  )
}
