import Home from "./Pages/Home"
import Wallpaper from "./Pages/Wallpaper"
import Miaochange from "./Pages/Miao"
import Qrcode from "./Pages/Qrcode"
import Vparse from "./Pages/Vparse"
import Feed from "./Pages/Feed"

const routes = [
  {
    path: '/',
    element: <Home />
  },
  {
    path: '/miao',
    element: <Miaochange />
  },
  {
    path: '/qrcode',
    element: <Qrcode />
  },
  {
    path: '/wallpaper',
    element: <Wallpaper />
  },
  {
    path: '/vparse',
    element: <Vparse />
  },
  {
    path: '/feed',
    element: <Feed />
  }
]
export default routes