import React, { useState } from 'react'
import { CapsuleTabs } from 'antd-mobile'
import styles from './index.module.less'
import Header from '../../components/Header'
export default function Feed() {
  const [url, setUrl] = useState('https://imgs-1302996373.cos.ap-shanghai.myqcloud.com/wx.jpg')
  const [tab, setTab] = useState('wx')
  const changeType = (e) => {
    switch (e) {
      case 'wx':
        setUrl('https://imgs-1302996373.cos.ap-shanghai.myqcloud.com/wx.jpg')
        setTab('wx')
        break;
      case 'qq':
        setUrl('https://imgs-1302996373.cos.ap-shanghai.myqcloud.com/qq.jpg')
        setTab('qq')
        break;
      default:
        setUrl('https://imgs-1302996373.cos.ap-shanghai.myqcloud.com/ali.jpg')
        setTab('alipay')
        break;
    }
  }
  return (
    <>
      <Header title='' />
      <div className={styles.container}>
        <img className={styles.qrcode} style={{ borderColor: tab === 'wx' ? '#07c160' : tab === 'qq' ? '#07bcfc' : '#1677ff' }} src={url} alt="" />
        <CapsuleTabs onChange={changeType} defaultActiveKey='wx'>
          <CapsuleTabs.Tab title='' key='wx' />
          <CapsuleTabs.Tab title='QQ' key='qq' />
          <CapsuleTabs.Tab title='' key='alipay' />
        </CapsuleTabs>
        <p>~~~</p>
      </div>
      <img className={styles.gif} src="https://pic1.zhimg.com/v2-0144b7adf58fea0c7481b4a1ee1c9474_b.webp" alt="" />
      <img className={styles.gif2} src="https://pic1.zhimg.com/v2-0144b7adf58fea0c7481b4a1ee1c9474_b.webp" alt="" />
    </>
  )
}
