import React, { useState } from 'react'
import styles from './index.module.less'
import Header from '../../components/Header'
import { Button, Dialog, Input } from 'antd-mobile'
export default function Qrcode() {
  const [text, setText] = useState('')
  const [size, setSize] = useState(100)
  const submit = () => {
    if (text === '') {
      Dialog.alert({
        content: ''
      })
    } else {
      window.open(`https://api.oick.cn/qrcode/api.php?text=${text}&size=${size}`)
    }
  }
  return (
    <div className={styles.container}>
      <Header title='' />
      <div className={styles.box}>
        <Input onChange={(e) => { setText(e) }} clearable placeholder='' />
        <p className={styles.tip}>:httphttps</p>
        <Input
          onChange={(e) => { setSize(e) }}
          defaultValue={size}
          min={1}
          type='number'
          clearable
          placeholder=''
        />
        <Button block color='primary' onClick={submit}></Button>
      </div>
    </div>
  )
}
