import React, { useEffect, useState } from 'react'
import Header from '../../components/Header'
import { Button, Modal, TextArea, Toast } from 'antd-mobile'
import styles from './index.module.less'
import Miao from 'miao-lang'
import { CopyToClipboard } from 'react-copy-to-clipboard'
export default function Miaochange() {
  const [disable, setDisable] = useState(true)
  const [value1, setValue1] = useState('')
  const [value2, setValue2] = useState('')
  const [copied, setCopied] = useState(false)
  const [change, setChange] = useState(false)
  const change1 = (e) => {
    setValue1(e)
    if (!change) {
      setValue2(Miao.encode(e))
    } else {
      setValue2(Miao.decode(e))
    }
    if (e === '') {
      setValue2('')
    }
  }
  const copy = () => {
    setCopied(true)
    Toast.show({
      content: ''
    })
  }
  useEffect(() => {
    Modal.alert({
      title: '',
      content:
        <div>
          <p>1.</p>
          <p>2.</p>
        </div>
    })
  }, [])
  useEffect(() => {
    if (value2 !== '') {
      setDisable(false)
    } else {
      setDisable(true)
    }
  }, [value2])
  return (
    <div className={styles.container}>
      <Header title='' />
      <div className={styles.box}>
        <p className={styles.title}>{change ? '' : ''}</p>
        <TextArea onChange={change1} rows={5} value={value1} placeholder={change ? '' : ''} />
        <Button onClick={() => { setChange(!change); setValue1(''); setValue2('') }} color='primary' fill='outline' block><svg t="1688027045656" className={styles.change} viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3336" width="48" height="48"><path d="M199.193331 345.611667c0 0 63.411407-98.646917 163.634656-151.569436 100.292202-52.922519 223.755294-37.703837 304.715203 13.607774 81.028862 51.311611 108.313364 94.636636 108.313364 94.636636l69.786485-40.10321c0 0 14.39524-7.19812 14.39524 9.63147L860.038278 523.641963c0 0 0 22.451778-16.863567 14.396239-14.121426-6.717446-167.405099-94.636636-215.666378-122.366236-26.461059-11.927913-3.221816-21.628336-3.221816-21.628336l67.387112-38.83467c0 0-38.457726-48.535493-94.670613-74.242464-60.120039-31.602573-116.470833-35.373017-185.434675-9.083242-45.038264 17.13838-98.029336 61.046011-136.144294 125.827889L199.193331 345.611667 199.193331 345.611667zM824.802168 678.399637c0 0-63.410807 98.61294-163.633656 151.568836-100.223848 52.922918-223.755694 37.670459-304.715603-13.641751-81.028862-51.311011-108.312764-94.636636-108.312764-94.636636l-69.786485 40.10361c0 0-14.39564 7.19812-14.39564-9.597493L163.95802 500.334365c0 0 0-22.416801 16.863567-14.396239 14.121825 6.718445 167.405099 94.637036 215.666178 122.366236 26.461059 11.927913 3.221816 21.663312 3.221816 21.663312l-67.387112 38.800693c0 0 38.457726 48.534893 94.670613 74.242464 60.121038 31.602573 116.470833 35.373017 185.434675 9.082842 45.038264-17.13838 98.029336-61.011434 136.144294-125.828488L824.802168 678.399637 824.802168 678.399637z" fill="#1677ff" p-id="3337"></path></svg></Button>
        <p className={styles.title}>{change ? '' : ''}</p>
        <TextArea readOnly rows={5} value={value2} />
        <CopyToClipboard
          onCopy={copy}
          text={value2}>
          <Button color='primary' disabled={disable} block></Button>
        </CopyToClipboard>
      </div>
      <img className={styles.cat} src="https://1-1302996373.cos.ap-nanjing.myqcloud.com/cat.png" alt="" />
    </div >
  )
}
