import React, { useState } from 'react'
import styles from './index.module.less'
import Header from '../../components/Header'
import { Button, Input, Toast, Mask, SpinLoading, List, Image } from 'antd-mobile'
import axios from 'axios'
export default function Vparse() {
  const [data, setData] = useState({
    author: '',
    avatar: '',
    cover: '',
    like: '',
    music: {
      author: '',
      avatar: '',
      url: '',
    },
    play: '',
    title: '',
    time: '',
    uid: ''
  })
  const [url, setUrl] = useState('')
  const [btndisabled, setBtndisabled] = useState(true)
  const [load, setLoad] = useState(false)
  const [info, setInfo] = useState('none')
  const nul = () => {
    setData({
      author: '',
      avatar: '',
      cover: '',
      like: '',
      music: {
        author: '',
        avatar: '',
        url: '',
      },
      play: '',
      title: '',
      time: '',
      uid: ''
    })
  }
  const change = (e) => {
    if (e === '' || e === null) {
      nul()
      setBtndisabled(true)
      setInfo('none')
    } else {
      setUrl(e)
      setBtndisabled(false)
    }
  }
  const submit = () => {
    if (url === '' || url === null) {
      Toast.show({ content: '' })
    } else {
      setLoad(true)
      axios.get(`https://api.oick.cn/video/api.php?url=${url}`).then(res => {
        setLoad(false)
        Toast.show({
          content: res.data.msg
        })
        setData(res.data)
        if (res.data.code === 200) {
          setInfo('block')
        } else {
          nul()
        }
      })
    }
  }
  return (
    <>
      <Header title='' />
      <div className={styles.container}>
        <Input onChange={change} clearable placeholder='' />
        <p style={{ fontSize: '12px', color: '#ccc' }}>,</p>
        <Button onClick={submit} block color='primary' disabled={btndisabled}></Button>
        <div className={styles.info} style={{ display: info }}>
          <List header='' mode='card'>
            <List.Item prefix={
              <Image
                src={data.avatar}
                style={{ borderRadius: '255px' }}
                fit='cover'
                width={50}
                height={50}
              />
            }
              description={`uid:${data.uid}`}
            >
              <p>{data.author}</p>
            </List.Item>
          </List>
          <List header='' mode='card'>
            <List.Item title={`${data.title} :${data.like}`}>
              <img className={styles.cover} src={data.cover} alt="" />
              <svg onClick={() => { window.location.href = data.play }} t="1688458297416" className={styles.playico} viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2283" width="48" height="48"><path d="M802.133333 85.333333h-580.266666C98.986667 85.333333 0 184.32 0 307.2v409.6c0 122.88 98.986667 221.866667 221.866667 221.866667h580.266666c122.88 0 221.866667-98.986667 221.866667-221.866667V307.2c0-122.88-98.986667-221.866667-221.866667-221.866667z m-92.16 440.32l-307.2 204.8c-3.413333 3.413333-6.826667 3.413333-10.24 3.413334-3.413333 0-6.826667 0-6.826666-3.413334-6.826667 0-10.24-6.826667-10.24-13.653333V307.2c0-6.826667 3.413333-13.653333 10.24-13.653333s13.653333-3.413333 17.066666 0l307.2 204.8c3.413333 3.413333 6.826667 6.826667 6.826667 13.653333s-3.413333 10.24-6.826667 13.653333z" fill="#bfbfbf" p-id="2284"></path></svg>
            </List.Item>
            <List.Item
              arrow={false}
              title={`bgm:${data.music.author}`}
              onClick={() => { window.location.href = data.music.url }}
            >
              <span style={{ fontSize: '12px', color: '#ccc' }}>Tips:bgmbgm</span>
            </List.Item>
          </List>
        </div>
      </div >
      <Mask visible={load}>
        <SpinLoading style={{ '--size': '40px', margin: '40vh auto 0 auto' }} color='white' />
      </Mask>
    </>
  )
}
