import React from 'react'
import styles from './index.module.less'
import { Button } from 'antd-mobile'
import { useNavigate } from 'react-router-dom'
const item = [
  { id: 1, url: '/miao', title: '' },
  { id: 2, url: '/qrcode', title: '' },
  { id: 3, url: '/wallpaper', title: '' },
  { id: 4, url: '/vparse', title: '' },
]

export default function Home() {
  const navigate = useNavigate()
  return (
    <div className={styles.container}>
      <p className={styles.title}></p>
      {
        item.map(i => (
          <Button key={i.id} block shape='rounded' onClick={() => { navigate(i.url) }}>{i.title}</Button>
        ))
      }
      <p className={styles.me}>design by Hui,<span onClick={() => { navigate('/feed') }} style={{ color: '#1677ff', cursor: 'pointer' }}></span></p>
    </div>
  )
}
